h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

* {
  box-sizing: border-box;
}

.page {
  scroll-behavior: smooth;
  min-width: 320px;
  overflow-x: hidden;
}

.page__body {
  margin: 0;
  transition: all .4s ease-in-out;
}

.page:has(.page__menu:target) {
  overflow-y: hidden;
}

.page:has(.page__menu:target) .top-bar__menu-link {
  opacity: 0;
  transform: scale(-1);
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translate(0);
}

.header {
  background-color: #3f5252;
  height: 100%;
  position: relative;
}

.header__content {
  --columns: 12;
  grid-template-columns: repeat(var(--columns), 70px);
  justify-content: center;
  column-gap: 30px;
  margin-inline: 55px;
  display: grid;
}

@media (width <= 1280px) {
  .header__content {
    --columns: 6;
    grid-template-columns: repeat(var(--columns), 1fr);
    margin-inline: 0;
    padding-inline: 40px;
  }
}

@media (width <= 767px) {
  .header__content {
    --columns: 2;
    grid-template-columns: repeat(var(--columns), 1fr);
    column-gap: 20px;
    padding-inline: 20px;
  }
}

.header__section-title {
  color: #f1f5f4;
  letter-spacing: 0;
  grid-column: 1 / 7;
  margin: 90px 0 0;
